import React from "react"
import { Link } from "gatsby"
import { graphql } from "gatsby"
import Img from "gatsby-image"




import Layout from "../components/layout"
import SEO from "../components/seo"

const BlogPage = ({data}) => (
  <Layout>
    <SEO title="Home" />
   
    <section className="latestrepo">

             <div className="container">
                 <div class=""><h2 class="title">Latest Posts</h2></div>
                   <br />
                 <div class="list is-hoverable">

{data.allMarkdownRemark.edges.map(post => (

<div className="list-item" key={post.node.id}>
                    <div class="columns">
                        
                         <div class="column is-10">

                        <article class="media">
                         <figure class="media-left">
                             <p class="image is-64x64 postimg">
                             <Img fluid={post.node.frontmatter.image.childImageSharp.fluid} />
                             </p>
                         </figure>
                         <div class="media-content">
                             <div class="content">
                             <p>
                             {post.node.excerpt}
                             </p>
                             </div>
                            
                         </div>
                         </article>


                         </div>
                         <div class="column is-1">
                             <Link to={post.node.frontmatter.path} className="button is-black is-small">Read more</Link>
                         </div>
                     </div>
                    </div>



))}

</div>
                     
             </div>
             </section>


    
  </Layout>
)


export const BlogpageQuery = graphql`
  query allBlogIndexQuery {
    allMarkdownRemark {
      edges {
        node {
          id
          frontmatter{
            path
            title
            author
            category
            tags 
            image {
              childImageSharp{
                fluid{
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
          excerpt
          }
          
        }
      }
  }
`

export default BlogPage
